import React from 'react';

function Header() {
    return(
        <h1>
            You have {lenTodo} task
        </h1>
    )
}

function TodoForm({addTodo}) {
    const [value, setValue] = React.useState('');
    
    const handleSubmit = e => {
        e.preventDefault();
        // if (!value) return;
        addTodo(value);
        setValue("");
    }

    return (
        <form className="form" onSubmit={handleSubmit}>
            <input 
                required
                type="text"
                value={value}    
                onChange={e => setValue(e.target.value)}
            />
            
            <button title="Add task">+</button>
        </form>
    )
}

function Todo({ todo, index, completeTodo, removeTodo }) {
    return (
        <>
        <div className="todo" style={{ 
                textDecoration: todo.isCompleted ? "line-through" : "none",
                color: todo.isCompleted ? "green" : ""
            }}>

            {todo.text}

        </div>
        <div className="comple-remove">
            {/* <button onChange={() => completeTodo(index)}>
                Complete
            </button> () => completeTodo(index)}  */  } 
            <input type="checkbox" onChange={() => completeTodo(index)}/>
            <button onClick={() => removeTodo(index)}>
                X
            </button>
        </div>
        </>
    )
}

let lenTodo;

function Component() {

    const removeTodo = index => {
        const newTodos = [...todos];
        newTodos.splice(index, 1);
        setTodos(newTodos);
    }

    const completeTodo = index => {
        const newTodos = [...todos];
        newTodos[index].isCompleted = true;
        setTodos(newTodos);
    }

    const addTodo = text => {
        const newTodos = [ ...todos, { text }];
        setTodos(newTodos)
    }

    const [todos, setTodos] = React.useState([
        { 
        text: 'learn JS',
        isCompleted: false
        },
        {
        text: 'learn React',
        isCompleted: false
        },
        {
        text: 'learn Redux',
        isCompleted: false
        }
    ]);
    
    lenTodo = todos.length;

    return(
        <div className="app">
            <Header />

            <div className="todo-list">

                <TodoForm addTodo={addTodo} />

                {todos.map((todo, index) => (

                    <div key={todo.text} className="todo-item" style={{ 
                        border: todo.isCompleted ? "1px solid green" : ""}} > 
                        <Todo
                            index={index}
                            todo={todo}
                            completeTodo={completeTodo}
                            removeTodo={removeTodo}
                        />
                    </div>

                ))}

            </div>
        </div>
    )
}

export default Component;